.NavbarItems {
    display: -webkit-box;
    -webkit-box-align: center;
    height: 30px;
    /* display: block; */
    /* justify-content: center; */
    /* align-items: center; */
    border-bottom: 1px black solid;
}

.brand-logo {
    /* margin-right: 20%; */
    position: absolute;
    left: 20px;
    /* margin: auto; */
    margin: auto;
    /* margin-left: 0.5rem; */
}

.menu {
    position: absolute;
    right: 175px;
    /* margin-right: 11rem; */
    display: flex;
    align-items: center;
    margin: auto;
}

.menu-li {
    margin-left: 40%;
    list-style: none;
}

.menu-a {
    text-decoration: none;
    color: black;
    cursor: pointer;
}

.menu-a:hover {
    border-bottom: 1px black solid;
}

.burger {
    position: absolute;
    right: 30px;
    display: none;
    width: auto;
    height: auto;
    margin-left: 40%;
    cursor: pointer;
    /* justify-content: right; */
}

.burger div {
    width: 20px;
    height: 2px;
    background: black;
    margin-top: 2px;
}

.menu-open {
    position: absolute;
    right: 175px;
    display: flex;
    align-items: center;
    margin: auto;
}

@media screen and (max-width: 768px) {
    .brand-logo {}
    .burger {
        display: block;
    }
    .menu {
        /* width: 0; */
        display: none;
    }
    .menu-open {
        position: absolute;
        top: 15px;
        right: 20px;
        height: 96.7%;
        overflow-y: hidden;
        flex-direction: column;
        border-left: 1px black solid;
        padding-left: 20px;
    }
    .menu-li {
        margin-left: 0;
        margin-top: 40px;
        list-style: none;
    }
}

